@import url('https://fonts.googleapis.com/css2?family=Patua+One&display=swap');

body {
  background-color: #00afdb;
  font-family: 'Patua One', cursive;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-direction: column;
  padding: 2rem;
}

.questionWrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  border: 0.4rem solid #004d61;
  border-radius: 2rem;
  overflow-y: visible;
  max-width: 60%;
  max-height: 60%;
  padding: 2rem;
}

.questionImg {
  width: 100%;
  height: 85%;
  object-fit: contain;
}

.optionForm {
  display: flex;
  justify-content: center;
  align-items: center;
}

.optionText {
  color: #004d61;
  font-size: 2.5rem;
}

.optionInput {
  width: 3.5rem;
  height: 3.5rem;
  background-color: rgba(255, 255, 255, 0.5);
  border: 2px solid #004d61;
  outline: none;
  padding: 0;
  font-size: 3rem;
  text-align: center;
  margin: 1rem;
  color: #004d61;
  font-family: 'Patua One', cursive;
}